.page {
  margin-bottom: 60px;

  & h2 {
    min-width: 250px;
    padding: 0;
    margin: 0;
    margin-bottom: 25px;
  }

  & h2 a {
    @include text-nav ($size:35px, $line:30px);
  }

  & h3 {
    width: 250px;
    font-size: 40px;
    font-weight: 700;
    line-height: 30px;
  }

  & h4 {
    padding: 0;
    margin: 0;
    margin-bottom: -15px;
    text-decoration: underline;
  }

  & p {}

  & a {}

  & b {}

  & strong {}

  & em {}

  & time {
    display: block;
    margin: 0;
    margin-bottom: 15px;
    padding: 0;
    color: $black;
    font-size: 14px;
    font-weight: 700;
    line-height: 14px;
  }

  & figure {
    padding: 0;
    margin: 0;
    width: 100%;
    margin-bottom: 20px;
  }

  & figure img {
    width: 100%;
    margin-bottom: 10px;
  }

  & figcaption {
    max-width: 550px;
  }
}

@media (min-width: $tablet-width) {
  .page {

    & h2 {
      width: 550px;
    }

    & figure img {
      width: 550px;
    }
  }
}

@media (min-width: $desktop-width) {
  .page {

    & h2 {
      width: 550px;
      margin-left: auto;
      margin-right: auto;
    }

    & p {
      width: 645px;
      margin-left: 15%;
    }

    & time {
      width: 550px;
      margin-left: auto;
      margin-right: auto;
    }

    & figure {
      width: 550px;
      margin-left: auto;
      margin-right: auto;
    }
  }
}